import { AuthenticationService } from './authentication.service';
import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import { User } from '../_models';
import { Observable } from 'rxjs/Observable';
import { HttpClient, HttpHeaders } from '@angular/common/http';


@Injectable()
export class UserService {
    // private headers = new Headers({ 'Content-Type': 'application/json' });
    headers: Headers = new Headers({
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${this.authService.getToken()}`,
        'Accept': 'application/json',
        'Access-Control-Allow-Headers': 'Content-Type',
        'Access-Control-Allow-Origin': '*',
    });
    uploadHeaders: HttpHeaders = new HttpHeaders({
        'Content-Type': 'multipart/form-data',
        'Authorization': `Bearer ${this.authService.getToken()}`,
        'Accept': 'application/json',
        'Access-Control-Allow-Headers': 'Content-Type',
        'Access-Control-Allow-Origin': '*',
    });
    private dataUrl = 'http://localhost:3000';
    constructor(private http: Http,
        private authService: AuthenticationService,
        private httpClient: HttpClient) { }

    getById(id: number) {
        return this.http.get('/api/users' + id);
    }

    getByToken(header: Headers) {
        var dataUrl = this.dataUrl + '/api/users';
        // console.log(header);
        return this.http.get(dataUrl, { headers: header }).catch(this.handleError);
    }

    register(user: User) {
        // console.log(user);
        return this.http.post('/api/users/register', user);
    }

    update(user: User) {
        return this.http.put(`/users/` + user.id, user);
    }

    delete(id: number) {
        return this.http.delete(`/users/` + id);
    }
    getSubmitDetail(quizId: number) {
        const headers: Headers = new Headers({
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${this.authService.getToken()}`,
            'Accept': 'application/json',
            'Access-Control-Allow-Headers': 'Content-Type',
            'Access-Control-Allow-Origin': '*',
        });
        const getUrl = `/api/submitdetail/${quizId}`;
        return this.http.get(getUrl, { headers: headers }).catch(this.handleError);
    }
    submitAnswers() {

    }

    uploadImg() {

    }

    // postForm(url, form) {
    //     var header1 = new Headers().set('Content-Type', 'multipart/form-data');
    //     return this.http.post(url, form, {
    //       headers: header1,
    //     });
    // }

    postFile(fileToUpload: File): Observable<boolean> {
        const endpoint = '/api/upload';
        var formData: FormData = new FormData();
        console.log(fileToUpload);
        formData.append('fileKey', fileToUpload, fileToUpload.name);
        console.log(formData);
        return this.httpClient
            .post(endpoint, formData, { headers: this.uploadHeaders })
            .map(() => { return true; })
            .catch((e) => this.handleError(e));
    }

    private handleError(error: Response) {
        return Observable.throw(error);
    }
}



